

button:focus {
  outline: none;
}

.game {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.status {
  margin-bottom: 10px;
}

.options-buttons {
  display: flex;
  justify-content: center;
  align-items: center;

}

.option-button {
  display: flex;
  justify-content: center;
  align-items: center;
  --size: 25px;
  height: var(--size);
  width: var(--size);
  border: none;
  margin: 0 5px;
  border-radius: 3px;
  background-color: #263238;
  color: #fff;
  text-align: center;
  vertical-align: middle;
}

.option-button:hover {
  background-color: #546E7A
}
.option-button:focus {
  background-color: #546E7A
}

.last-move-button {
  border: none;
  padding: 2px 6px;
  margin-bottom: 3px;
  border-radius: 3px;
  background-color: #263238;
  color: #fff;
}

.last-move-button:hover {
  background-color: #546E7A
}
.last-move-button:focus {
  background-color: #546E7A
}

@keyframes spin {
  from {transform:rotate(0deg);}
  to {transform:rotate(360deg);}
}